
import axios from 'axios';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

export const newsList = createAsyncThunk('news_getList', async(payload, {rejectedWithValue})=>{
    let result = null;
    try {
        result = await axios.get("https://newsapi.org/v2/top-headlines", {
            params : {
                country : 'kr',
                apikey : 'e256a8122ec34bb18ad5654995b6f583',
                category : payload
            },
        });
    }catch(err){
        result = rejectedWithValue(err.respnose);
    }
    return result;
});

export const newsSlice = createSlice({
    name : 'news',

    initialState : {
        rt : null,
        rtmsg : null,
        item : [],
        loading : false
    },

    reducers : {},

    extraReducers : {
        [newsList.pending] : (state, {payload})=>{
            return {
                ...state,
                loading : true,
            }
        },
        [newsList.fulfilled] : (state, {payload})=>{
            return {
                ...state,
                rt : payload.status,
                rtmsg : payload.statusText,
                item : payload.data,
                loading : false
            }
        },
        [newsList.rejected] : (state, {payload})=>{
            return {
                ...state,
                rt : payload.status ? payload.status : 500,
                rtmsg : payload.statusText ? payload.statusText : 'SeverError',
                item : payload.item,
                loading : false,
            }
        }
    }
});

export default newsSlice.reducer;
